window.handleClickCollapse = function (data) {
  const parent = document.querySelector(data);
  // console.log(window.getComputedStyle(parent).display);
  if (window.getComputedStyle(parent).display == "flex") {
    const child = parent.querySelector(".collapse-content");
    parent.classList.toggle("collapsed");
  }
};
/******************** End Footer Section */
